.header-wrapper {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
  padding: 12px 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 24px;

  h1 {
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 0;
    
  }
  .left {
    flex: 1;
    margin-left: -10px;
  }
  .main {
    flex: 1;
    text-align: center;
  }
  .right {
    flex: 1;
    text-align: right;
  }
}